import React from 'react'
import Affair from './Affair'
import {AffairType, FilterType} from './HW2'
import s from './Affairs.module.css'

type AffairsPropsType = { // need to fix any
    data: Array<AffairType>
    filter: FilterType
    setFilter: (filter: FilterType) => void
    deleteAffairCallback: (_id: number) => void
}

function Affairs(props: AffairsPropsType) {
    const mappedAffairs = props.data.map((a: AffairType) => (
        <Affair // should work
            key={a._id} // кеи ОБЯЗАТЕЛЬНЫ в 99% - так что лучше их писать всегда при создании компонент в мапе
            affair={a}
            deleteAffairCallback={props.deleteAffairCallback}
        />
    ))

    const setAll = () => {
        props.setFilter('all')
    } // need to fix
    const setHigh = () => {
        props.setFilter('high')
    }
    const setMiddle = () => {
        props.setFilter('middle')
    }
    const setLow = () => {
        props.setFilter('low')
    }

    const setOnAll = s.buttons + ' ' + (props.filter === 'all' ? s.active : '')
    const setOnHigh = s.buttons + ' ' + (props.filter === 'high' ? s.active : '')
    const setOnMiddle = s.buttons + ' ' + (props.filter === 'middle' ? s.active : '')
    const setOnLow = s.buttons + ' ' + (props.filter === 'low' ? s.active : '')
    /*const setButton = (filter: FilterType) => {
        return s.buttons + (props.filter === filter ? '' + s.active : '')*/

    return (
        <div>
            <div>
                {mappedAffairs}
            </div>
            <div>
                <button className={setOnAll} onClick={setAll}>All</button>
                <button className={setOnHigh} onClick={setHigh}>High</button>
                <button className={setOnMiddle} onClick={setMiddle}>Middle</button>
                <button className={setOnLow} onClick={setLow}>Low</button>
            </div>
        </div>
    )
}

export default Affairs;
